.main-box{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(4,1fr);
        background-color: #52DED7;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        border-radius: 10%;

    } 

h1 h2{
        font-family: 'Delius Unicase', cursive;
    }
p{
    font-family: 'Roboto', sans-serif;
}
.RoutineBox {
    display: grid;
    margin: 2vh;
    margin-left: 25vh;
    margin-right: 25vh;
    padding: 5vh;
    align-items: center;
    background-color: #52DED7;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border-radius: 3vh;
}
.activity-form{
    display: grid;
    background-color: #48A9A6;
    margin: 2vh;
    margin-left: 30vh;
    margin-right: 30vh;
    padding: 5vh;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border-radius: 20%;
    
}

.home-page{
    font-family: 'Grand Hotel', cursive;
    display: flex;
    background:linear-gradient(#33ccff , #52DED7);
    justify-content:center;
    align-items:center;
    position: relative;top:25vh;
    width: 50vh;
    height: 40vh;
    margin: auto;
    border-radius: 25%;
    border-style: double;
    text-align: center;
    color: #004E64;
    
}
header{
    background-color: #9FFFCB;
    font-family: 'Grand Hotel', cursive;
    color: #2B3964;
    font-size: larger;
}

.navigation{
    background-color: #25A18E;
    font-family: 'Oswald', sans-serif;
   
}
